#home {
	section.hero {
		@include bp-medium {
			// bp-medium is like a media query for medium screens
			// use rem(pixels, $text-bp-md) for medium font sizes, widths, padding etc
			// use rem(pixels, $text-bp-sm) for small font sizes, widths, padding etc 
			height: rem(748, $text-bp-md);		
		}
		.preheader, .header {
			padding:  rem(20, $text-bp-sm) rem(50, $text-bp-sm);
			@include bp-medium {
				padding-top:  rem(50, $text-bp-md);
				padding-left:  rem(136, $text-bp-md);	
			}
		}
		.header {
			font-weight: bold;  
		}  
		.section-inner {
			@include bp-medium{
				display: flex;
	    	justify-content: flex-end;
			}
			//fix positioning
			#house-img {
				position:absolute;
				width: 100%;
				width: 384px;
	    		height: 174.5px;
	    		padding-top: 100px;
				@include bp-medium {
					width: rem(768, $text-bp-md);
				@include bp-large {
					width: rem(768, $text-bp-lg);
					}
				}
			}
	  }
	}
	// adjust for large and small screens?
	section.sub-hero {  
		@include bp-medium {
			background-color: #f9f4f4;
			font-size: rem(32px);
			font-weight: 400;
			height: 1150px;
		}
		.header{
			@include bp-medium{
				padding-left:130px;
				font-size: 45px;
				position: absolute;
				padding-top:20px;
				}
			@include bp-large{
				padding-left: 250px;
				padding-top: 160px;
				}
		}
		.body-text{
			position: absolute;
		    padding-left: 700px;
		    padding-top: 580px;
		    @include bp-large{
		    	padding-left: 900px;
		    	padding-top: 600px;
	    	}
		}
		.btn-lg-red {
			cursor: pointer;
			text-align: center;
			background-color: $brick;
			color: $white;
			width: rem(256, $text-bp-sm);
			font-size: rem(22, $text-bp-sm);
			padding: rem(20px, $text-bp-sm) rem(25px, $text-bp-sm);
			border-radius: rem(5, $text-bp-sm);
			margin:  rem(20, $text-bp-sm) auto;

			@include bp-medium {
				margin-left: 700px;
				width: rem(256, $text-bp-md);
				font-size: rem(22, $text-bp-md);
				padding: rem(25px, $text-bp-md) rem(25px, $text-bp-md);
				border-radius: rem(5, $text-bp-md);
				}
				@include bp-large{
					margin-left: 900px;
					margin-bottom: 300px;
				}
			}
			#tanja-img {
		      	display: flex;
		      	justify-content: flex-start;
		      	padding-top:53%;
		        width: 100%;
		        width: 322px;
		        height: 483px;
				@include bp-large{
					padding-top: 38%;
				}
			}
	}
		
	section.our-services{   
		background-color: #f9f4f4;
		.header-services {
			margin-left: 130px;
			padding-bottom: 65px;
			font-size: 20px;
		}
		// Make space in between columns thinner? change color of border
		// should be 2px
		.container{
			width: 100%;
			// Move numbers to the left and thin out font. Move text down.
			.item{
				color: $white;
				text-align: center;
				font-size: 16px;
				height: 300px;
				float: left;
				width: 21%;
				padding: 1%;
				margin: 0 1%;
				background:#2c2424;
			}
		}
	}	
	section.history {
		background: #f9f4f4;
		.body-text2 {
			position:absolute;
			height: 600px;
			font-size: 25px;
			padding-top: 100px;
		}
		#chris-img {
			padding-left:53%;
			width:100%;
			width:486px;
			height:614px;
			@include bp-large{
				padding-left: 58%;
			}
		}
	}
	.bold{
		font-weight: bold; 
	}
			
	// Move button down
	.btn-lg-red1 {
		position: absolute;
		padding-top: 300px; 
		cursor: pointer;
		text-align: center;
		background-color: $brick;
		color: $white;
		width: rem(256, $text-bp-sm);
