		padding: rem(20px, $text-bp-sm) rem(25px, $text-bp-sm);
		border-radius: rem(5, $text-bp-sm);
		margin:  rem(20, $text-bp-sm) auto;

		@include bp-medium {
			position: absolute;
			padding-top: 300px;
			// margin:  0 auto;
			width: rem(256, $text-bp-md);
			font-size: rem(22, $text-bp-md);
			padding: rem(25px, $text-bp-md) rem(25px, $text-bp-md);
			border-radius: rem(5, $text-bp-md);
		}
	}

	section.recent-projects{
		@include bp-large{
			background-color: $white;
			height: 2000px;
		}
		.header {
			position: absolute;
			// padding-top:70px;
			padding-left: 100px;
			font-size: 40px;
		}
		#rob-apt-img{
			display: flex;
			justify-content: flex-end;
			padding-bottom: 400px;
			padding-left: 450px;
			width: 100%;
			width: 572px;
			height: 414px;
			@include bp-large{
				padding-left: 600px;
			}
		}
		.rob{
			position: absolute;
			padding-top: 380px;
			padding-left: 100px;
			font-size: 18px;
			@include bp-large{
				padding-top: 200px;
			}
		}
				//needs top line to be bold
		.westside-text{
			position: absolute;
			padding-top: 320px;
			padding-left: 800px;
			font-size: 18px;
			@include bp-large{
				padding-left: 950px;
			}
		}
		#westside-courts-img{
			width: 100%;
			width: 684px;
			height: 478px;
			@include bp-large{
